.container {
    position: relative;
}

.insideContainer {
    position: relative;
    overflow: hidden;
    height: calc(100vh - 50px);
    width: 100%;
    margin: 0 auto;
}

/*=======CIRCLES ANIMATION===========*/

.circle {
    position: absolute;
    border-radius: 50%;
    animation: circle-scale 3s ease-in-out infinite alternate;
    animation-timing-function: cubic-bezier(.6, 0, .4, 1);
}

.small {
    animation-delay: 0s;
    top: 13rem;
    left: -150px;
    background: #34416d;
    width: 20rem;
    height: 20rem;
    opacity: 0.4;
}

.medium {
    animation-delay: 0.3s;
    top: 3.3rem;
    left: -20rem;
    background: #303f6b;
    width: 40rem;
    height: 40rem;
    opacity: 0.3;
}

.large {
    animation-delay: 0.6s;
    top: -6.6rem;
    left: -30rem;
    background: #3d4d82;
    width: 60rem;
    height: 60rem;
    opacity: 0.2;
}

.xLarge {
    animation-delay: 0.9s;
    top: -16rem;
    left: -40rem;
    background: #6482e2;
    width: 80rem;
    height: 80rem;
    opacity: 0.1;
}

.xxLarge {
    animation-delay: 1.2s;
    top: -26rem;
    left: -50rem;
    background: #18203a;
    width: 100rem;
    height: 100rem;
    opacity: 0.05;
}

@keyframes circle-scale {
    0% {
        transform: scale(1.0);
    }
    100% {
        transform: scale(1.1);
    }
}

/*=========LOGIN=========*/

.loginForm {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 35%;
    background: rgba(69, 73, 109, 0.8);
    padding: 1.75rem;
    color: #8c91b6;
}

.loginForm input {
    height: 2.5rem;
    background: transparent;
    border: 1px solid #8c91b6;
    font-size: 14px;
    color: #8c91b6;
    border-radius: 10px;
    padding: 0 5px;
    margin: 5px 0;
}

.loginForm input:focus {
    outline: none;
    border-color: #fff;
}

.loginForm input[type="checkbox"] {
    height: 15px;
}

.loginForm p {
    margin: 1rem 0;
}

.captcha {
    width: 100px;
    height: 50px;
}

.loginForm button{
    color: #fff;
    background-color: #fd5e3aa1;
    width: 5rem;
    display: inline-block;
}

@media (max-width:768px) {
    .loginForm{
     position: relative ;
     width: 100%;
    }
    .container{
      display: none;
    }
  }